.grid-wrapper {
  position: relative;
  border-radius: 18px;
  padding-bottom: 30px;
  -webkit-box-shadow: 0 -5px 50px 0 rgba(0,0,0,0.05),0 -2px 15px 0 rgba(0,0,0,0.05);
          box-shadow: 0 -5px 50px 0 rgba(0,0,0,0.05),0 -2px 15px 0 rgba(0,0,0,0.05);
}

@media all and (min-width: 650px) {
  .grid-wrapper {
    margin-top: 60px;
  }
}

.grid-wrapper-bottom {
  position: absolute;
  bottom: -35px;
  background-color: white;
  height: 40px;
  width: 100%;
}

.grid-container {
  display: grid;
  padding: 50px;
  padding-bottom: 100px;
  grid-gap: 50px;
  -ms-flex-pack: center;
      justify-content: center;
}

@media all and (max-width: 375px) {
  .grid-container {
    padding: 50px 25px;
  }
}

/* INTRO */

.intro-text {
  color: #666666;
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  font-size: 16px;
}

/* TODO: Kinda hacky. Find a better way to achieve same behavior */
@media all and (min-width: 849px) {
  .intro-title {
    padding-top: 85px;
  }
}

@media all and (min-width: 1000px) {
  .intro-title {
    padding-top: 0;
  }
}



